.navigation{display:flex;justify-content:space-evenly;align-items:center;padding:10px;margin-bottom:50px;background:white;position:-webkit-sticky;position:sticky;top:0}@media (min-width: 500px){.navigation{justify-content:flex-end}.navigation a{margin-right:20px}}.navigation img{width:30px}@media (min-width: 500px){.navigation img{width:40px}}.navigation .primary-button{font-size:14px}@media (min-width: 500px){.navigation .primary-button{font-size:16px}}

.primary-button{border-radius:6px;padding:6px 10px;color:white;border:0;font-family:"Roboto",sans-serif;font-weight:bold;background:#659dd4}.primary-button:focus{outline-style:dotted;outline-width:medium;outline-offset:5px;outline-color:#a4a4a4}.secondary-button{border-radius:6px;padding:6px 10px;color:white;border:0;font-family:"Roboto",sans-serif;font-weight:bold;background:#df957c;font-size:20px}.secondary-button:focus{outline-style:dotted;outline-width:medium;outline-offset:5px;outline-color:#a4a4a4}@media (min-width: 768px){.primary-button:hover,.secondary-button:hover{transition:ease .3s}.primary-button:hover{background:#4c89c4}.secondary-button:hover{background:#dd7f60}}

.opening-text .emoji{font-size:60px}.opening-text h1,.opening-text h2 code{font-weight:300}.opening-text h1{font-size:34px}.opening-text h1 .design{color:#df957c;font-family:"Shadows Into Light Two",cursive}.opening-text h2{color:#a4a4a4;font-size:22px;margin:40px 0}

@media (min-width: 768px){.skeleton{display:flex}}.skeleton .left,.skeleton .right{margin:0 30px}.skeleton .left{text-align:center;margin-bottom:50px}@media (min-width: 768px){.skeleton .left{flex:1 1;text-align:left;margin-bottom:0px}}.skeleton .left .hobbies{padding-left:0;margin-bottom:40px}@media (min-width: 768px){.skeleton .left .hobbies{padding-left:20px}}.skeleton .left .hobbies li{margin:6px 0;list-style-type:none}@media (min-width: 768px){.skeleton .left .hobbies li{list-style-type:circle}}@media (min-width: 768px){.skeleton .right{flex:2 1}}@media (min-width: 1150px){.skeleton .right{margin-right:250px}}.skeleton .right .essay{margin-bottom:50px}.skeleton .right .essay h3{margin:0 0 10px 0;font-size:26px}.skeleton .right .essay .date-time{color:#a4a4a4}.skeleton .right .essay .content{line-height:22px}

body{font-family:"Roboto",sans-serif;margin:0}a{color:#659dd4}a:hover{text-decoration:none}

